      return this.$store.state.proyects
    }
  },
  methods: {
    sendVillInfo () {
      this.$buefy.toast.open({
        message: 'Gracias ' + this.name + '!! Nos pondremos en contacto para coordinar una videollamada',
        type: 'is-success',
        duration: 3000
      })

      this.state1 = true
      this.state2 = false
      // eslint-disable-next-line no-console
      console.log(this.msg)
    },
    sendWhatsappInfo () {
      this.$buefy.toast.open({
        message: 'Gracias ' + this.name + '!! Nos pondremos en contacto por Whatsapp.',
        type: 'is-success',
        duration: 3000
      })

      this.state1 = true
      this.state3 = false
    },
    sendMailInfo () {
      this.$buefy.toast.open({
        message: 'Gracias ' + this.name + '!! Nos pondremos en contacto vía Mail',
        type: 'is-success',
        duration: 3000
      })

      this.state1 = true
      this.state4 = false
    }
  }
}
